<template>
<div class='twitch-embed'>
	<ul role="menubar">
		<li v-for="member in memberList"v-if="checkRole(member)" role="menuitem">
			<button v-on:click='setTargetStream(member)' v-bind:class="{ selected: isSelected(member), 'not-live': !isLive(member) }" :aria-label="'change to ' + member">{{member}}
					<span v-if='isLive(member)' class="live">
						<div :aria-label="getViewers(member) + ' viewers'">{{getViewers(member)}}
							<svg height="16px" version="1.1" viewBox="0 0 16 16" width="16px" x="0px" y="-10px" class="svg-glyph_live">
								<path clip-rule="evenodd" d="M11,14H5H2v-1l3-3h2L5,8V2h6v6l-2,2h2l3,3v1H11z" fill-rule="evenodd"></path>
							</svg>
						</div>
					</span>
			</button>
		</li>
	</ul>
	<div class="twitch-wrapper">
		<div class='twitchStream'>
			<iframe :src="targetStreamUrl" frameborder="0" scrolling="no" v-show="targetedStream !== ''"></iframe>
		</div>
		<div class='twitchChat'>
			<iframe frameborder="0" scrolling="no" :src="targetChatUrl" v-show="targetedStream !== ''"></iframe>
		</div>
	</div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Members from '../../../static/memberInfo.json'

export default {
	name: 'twitch-embed',
	data () {
		return {
			targetedStream: this.target ? this.target : ''
		}
	},
	methods: {
		setTargetStream (channel) {
			this.targetedStream = channel
		},
		isSelected (channel) {
			return this.targetedStream === channel
		},
		checkRole (channel) {
			return Members[channel].role === 'streamer'
		},
		isLive (channel) {
			return this.twitchData[Members[channel].id] && this.twitchData[Members[channel].id].live
		},
		getViewers (channel) {
			return this.twitchData[Members[channel].id].live.viewers
		},
		...mapActions([
			'updateChannelInfoList'
		])
	},
	computed: {
		twitchData () {
			return this.$store.state.TwitchInfo
		},
		targetStreamUrl () {
			return this.targetedStream === '' ? '' : 'https://player.twitch.tv/?channel=' + this.targetedStream
		},
		targetChatUrl () {
			return this.targetedStream === '' ? '' : 'https://www.twitch.tv/' + this.targetedStream.toLowerCase() + '/chat'
		},
		memberList () {
			var liveCollection = []
			var offlineCollection = []
			// Sort the collections into online and offline
			for (var member in Members) {
				if (this.twitchData[Members[member].id] && this.twitchData[Members[member].id].live) {
					liveCollection.push(member)
				} else {
					offlineCollection.push(member)
				}
			}
			// sort the live streams
			liveCollection.sort(function (a, b) {
				return this.twitchData[Members[b].id].live.viewers - this.twitchData[Members[a].id].live.viewers
			}.bind(this))
			// then tack on the remainders
			return liveCollection.concat(offlineCollection)
		},
		...mapGetters([
			'twitchInfo'
		])
	},
	props: ['target'],
	mounted () {
		this.updateChannelInfoList()
		.then(function () {	console.log('Twitch data successfully fetched')	})
		.catch(function () { console.log('There was a problem fetching Twitch data') })
		.then(function () {
			this.targetedStream = this.target ? this.target : this.memberList[0]
		}.bind(this))
	}
}
</script>

<style scoped lang="scss">
@import "../../variables.scss";

.twitch-wrapper{
	position: relative;
	.twitchStream {
		position: relative;
		width:75%;
	}
	.twitchStream:before{
		content: "";
		display: block;
		padding-top: 56.25%; /* 16:9 */
	}
	.twitchChat{
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		width: 25%;
	}
	.twitchStream iframe, .twitchChat iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
button{
	padding: 5px 10px;
	cursor: pointer;
	line-height: 30px;
	transition: $standard-transition;
	text-transform: capitalize;
	&:hover, &:focus{
		background-color: $gray-light;
	}
	&:active{
		background-color: $gray-dark;
	}
}
li {
	display: inline-block;
	vertical-align: middle;
}
li:not(:last-child){
	border-right: 1px solid $font-white;
}
.selected, .selected:hover, .selected:focus{
	background-color: $gray-light;
	transition: all .1s ease-in-out;
}
@media (max-width: 1024px) {
	.twitch-wrapper{
		.twitchStream{
			width:100%;
		}
		.twitchChat{
			display:none;
		}
	}
}
@media screen and (max-width: 52.000em){
	.not-live{
		line-height: 59px;
	}
	.live{
		display: block;
	}
}
</style>



