<template>
	<div class="footer-bar">
		<div class="logo-wrap">
			<img class="gold-logo" src="../../assets/graphics/logo-small-color.png" alt="catalyst-logo-gold"/>
			<span>TEAMCATALYST.TV</span>
			<a href="mailto:manager@teamcatalyst.tv" class="yellow">Contact Us</a>
			<button class="elevator-button" v-on:click="backToTop" v-if="showBackToTop" aria-label="Return to Top" tabindex="0"></button>
		</div>
		<social-media-icons :iconColor="'#4f5154'" :size="'20px'" class="social"></social-media-icons>
	</div>
</template>

<script>
import SocialMediaIcons from './SocialMediaIcons'
import Elevator from 'elevator.js'

export default {
	name: 'footer-bar',
	components: {SocialMediaIcons},
	methods: {
		backToTop: function () {
			var elevator = new Elevator({
				mainAudio: '../static/music/elevator.mp3',
				endAudio: '../static/music/ding.mp3'
			})
			elevator.elevate()
		}
	},
	props: ['showBackToTop']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
button {
  -webkit-appearance: none;
  background: transparent;
  border: 0;
  outline: 0;
  padding: 0;
}
.footer-bar{
	position: absolute;
	text-align: left;
	background-color: $gray-dark;
	height: 60px;
	right: 0;
	bottom: 0;
	left: 0;
}
.logo-wrap{
	margin: 4px 25px 3px 25px;
	transition: $standard-transition;
	.gold-logo{
		width:75px;
		vertical-align: middle;
	}
	a{
		margin: 0 0 0 15px;
	}
}
.elevator-button {
	display: inline-block;
	border-bottom: 10px solid $yellow-dark;
	border-left: 4px solid transparent;
	border-right: 5px solid transparent;
	cursor: pointer;
	margin-left: 15px;
	vertical-align: middle;
	&:hover, &:focus, &:active		{
		-webkit-filter: drop-shadow(0 0 1px $font-white);
		-moz-filter: drop-shadow(0 0 1px $font-white);
		-ms-filter: drop-shadow(0 0 1px $font-white);
		-o-filter: drop-shadow(0 0 1px $font-white);
			filter: drop-shadow(0 0 1px $font-white);
		border-bottom: 10px solid $yellow-light;
	}
}
.social{
	position: absolute;
	right: 25px;
	top: 7%;
	transition: $standard-transition;
}
@media screen and (max-width: 55.000em){ /*960px*/
	.social{
		display: none;
	}
	.logo-wrap {
		margin: 4px 10px 3px 10px;
	}
}
</style>


