<template>
	<div class="members">
		<section-header :sectionName="sectionName"></section-header>
		<div class="streamer-list-wrapper">
			<member-tile v-for="(value, key) in members" :streamer='key'></member-tile>
		</div>
	</div>
</template>

<script>
import { mapActions } from 'vuex'
import Members from '../../../static/memberInfo.json'
import SectionHeader from './SectionHeader'
import MemberTile from '../members/MemberTile'

export default {
	name: 'members',
	data () {
		return {
			members: Members,
			sectionName: 'MEMBERS'
		}
	},
	components: {
		SectionHeader,
		MemberTile
	},
	methods: {
		...mapActions([
			'updateChannelInfoList'
		])
	},
	mounted () {
		this.updateChannelInfoList()
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.streamer-list-wrapper{
	margin: 0 auto;
	padding-bottom: 44px;
	max-width: 1200px;
	text-align: center;
}
</style>


