<template>
	<div class="team-event">
		<div>
			<div class="yellow">TEAM EVENT</div>
			<div class="event-title">{{event.title}}</div>
			<div class="date-time-wrapper">
				<div class="date-time-row">
					<svg viewBox="0 0 60 60" class="icon calendar">
						<g>
							<path d="M57,4h-7V1c0-0.553-0.447-1-1-1h-7c-0.553,0-1,0.447-1,1v3H19V1c0-0.553-0.447-1-1-1h-7c-0.553,0-1,0.447-1,1v3H3
								C2.447,4,2,4.447,2,5v11v43c0,0.553,0.447,1,1,1h54c0.553,0,1-0.447,1-1V16V5C58,4.447,57.553,4,57,4z M43,2h5v3v3h-5V5V2z M12,2h5
								v3v3h-5V5V2z M4,6h6v3c0,0.553,0.447,1,1,1h7c0.553,0,1-0.447,1-1V6h22v3c0,0.553,0.447,1,1,1h7c0.553,0,1-0.447,1-1V6h6v9H4V6z
								 M4,58V17h52v41H4z"/>
							<path d="M38,23h-7h-2h-7h-2h-9v9v2v7v2v9h9h2h7h2h7h2h9v-9v-2v-7v-2v-9h-9H38z M31,25h7v7h-7V25z M38,41h-7v-7h7V41z M22,34h7v7h-7
								V34z M22,25h7v7h-7V25z M13,25h7v7h-7V25z M13,34h7v7h-7V34z M20,50h-7v-7h7V50z M29,50h-7v-7h7V50z M38,50h-7v-7h7V50z M47,50h-7
								v-7h7V50z M47,41h-7v-7h7V41z M47,25v7h-7v-7H47z"/>
						</g>
					</svg>
					<span class="date-time highlight">{{weekDate}} {{day}} {{month}}</span>
				</div>
				<div class="date-time-row">
					<svg viewBox="0 0 60 60" class="icon clock">
						<g>
							<path d="M30,0C13.458,0,0,13.458,0,30s13.458,30,30,30s30-13.458,30-30S46.542,0,30,0z M30,58C14.561,58,2,45.439,2,30
								S14.561,2,30,2s28,12.561,28,28S45.439,58,30,58z"/>
							<path d="M30,6c-0.552,0-1,0.447-1,1v23H14c-0.552,0-1,0.447-1,1s0.448,1,1,1h16c0.552,0,1-0.447,1-1V7C31,6.447,30.552,6,30,6z"/>
						</g>
					</svg>
					<span class="date-time">{{time}} {{timezone}}</span>
				</div>
			</div>
			<div  class="event-notes" v-if="event.notes"v-html="event.notes"></div>
		</div>
		<div class="icon-row">
			<img :src="getLogo(subCal)" v-if="getLogo(subCal)" v-for="subCal in event.subcalendar_ids" :class="getDisplayName(subCal)" :alt="getDisplayName(subCal)">
		</div>
	</div>
</template>

<script>
import MomentTZ from 'moment-timezone'
import Members from '../../../static/memberInfo.json'
import DateTimeSlug from './DateTimeSlug'
export default {
	name: 'team-events',
	computed: {
		eventMoment: function () {
			return MomentTZ(this.event.start_dt)
		},
		weekDate: function () {
			return this.eventMoment.format('dddd')
		},
		day: function () {
			return this.eventMoment.format('D')
		},
		month: function () {
			return this.eventMoment.format('MMM')
		},
		time: function () {
			return this.eventMoment.format('LT')
		},
		timezone: function () {
			return MomentTZ.tz(MomentTZ.tz.guess()).format('z')
		}
	},
	methods: {
		getDisplayName: function (calendarId) {
			for (var member in Members) {
				if (Members[member].calendar_id === calendarId) {
					return member
				}
			}
			return 'non-member'
		},
		getLogo: function (calendarId) {
			var name = this.getDisplayName(calendarId)
			if (name !== 'non-member') {
				var twitchId = Members[name].id
				return this.$store.state.TwitchInfo[twitchId] ? this.$store.state.TwitchInfo[twitchId].logo : '../../../static/profiles/' + name + '.png'
			}
			return null
		}
	},
	components: {
		DateTimeSlug
	},
	props: ['event']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.team-event{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background-color: $gray-dark;
	border-left: 2px solid $yellow-dark;
  box-shadow: $bs;
	padding: 15px 10px;
	max-height: 450px;
	flex: 1 1 280px;
}
.date-time{
	color: $font-gray;
}
.date-time-row{
	margin: 7px 0;
}
.event-title{
	margin: 10px 0;
}
img{
	border-radius: 100px;
	width: 50px;
	border: 2px solid $gray-light;
	padding: 3px;
	margin: 10px 10px 0 0;
	transition: all .2s ease-in-out;
	vertical-align: middle;
}
.icon{
	display: inline-block;
	margin-right: 5px;
	width: 20px;
	height: 20px;
	fill: $yellow-dark;
	&.calendar{
		vertical-align: text-bottom; 
	}
	&.clock{
		vertical-align: text-top; 
	}
}
.title{
	margin: 5px 0;
}
.highlight{
	color: $font-white;
}
.lowco2525{
	color: $lowco-purple;
	border-color: $lowco-purple;
}
.smashley{
	color: $smashley-blue;
	border-color: $smashley-blue;
}
.djtechlive{
	color: $dj-blue;
	border-color: $dj-blue;
}
.bloodyfaster{
	color: $bloody-red;
	border-color: $bloody-red;
}
</style>


