<template>
	<div class="router-page">
		<header-bar :showGold="true"></header-bar>
		<team-intro></team-intro>
		<members class="section"></members>
		<social-media class="section"></social-media>
		<calendar class="section"></calendar>
		<footer-bar :showBackToTop="true"></footer-bar>
	</div>
</template>

<script>
import HeaderBar from './globals/Header'
import TeamIntro from './home/TeamIntro'
import Members from './home/Members'
import SocialMedia from './home/SocialMedia'
import Calendar from './home/Calendar'
import FooterBar from './globals/Footer'

export default {
	name: 'home',
	components: {
		HeaderBar,
		TeamIntro,
		Members,
		SocialMedia,
		Calendar,
		FooterBar
	}
}
</script>

<style lang="scss">
@import '../variables.scss';
html {
	box-sizing: border-box;
}
body {
	font-family: $font-stack;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	letter-spacing: 0.15em;
	text-align: center;
	background-color: $gray-med;
	color: $font-white;
	min-width: 500px;
	min-height: 100%;
	height: 100%;
	margin: 0;
}
button {
	-webkit-appearance: none;
	background: transparent;
	font-family: $font-stack;
	font-size: 100%;
	letter-spacing: 0.15em;
	color: $font-white;
	border: 0;
	outline:0;
}
@font-face {
	font-family: 'ratio';
	src: url('../assets/fonts/ratio.woff2') format('woff2'),
			 url('../assets/fonts/ratio.woff') format('woff'),
			 url('../assets/fonts/ratio.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
.yellow{
	color: $yellow-dark;
}
a.yellow:hover, a.yellow:focus{
	color: $yellow-light;
}
.section{
	min-height: 530px;
	text-align: left;
}
#app {
	position: relative;
	min-height: 100%;
}
.router-page {
	padding-bottom: 60px;
}
.parallax {
	background-image: url('../assets/graphics/welcome-background.png');
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-size: cover;
}
.live {
	display: inline-block;
	line-height: normal;
	background-color: $live-red;
	padding: 5px 10px;
	border-radius: 10px;
	white-space: nowrap;
}
.svg-glyph_live {
	fill: $font-white;
	vertical-align: middle;
}
a, p{
	text-decoration: none;
	color: $font-white;
	margin: 0;
	outline: none;
}
ul{
	cursor:default;
	margin: 0;
	padding: 0;
}
h1, h2, h3{
	font-weight: lighter;
}
h2{
	text-transform: capitalize;
}
h2, h3{
	color: $yellow-dark;
	font-size: 26px;
	margin: 0;
}
h3{
	font-size: 16px;
	margin: 5px 0;
}
</style>


