<template>
	<div class="slug">
		<span class="yellow">{{weekDate}}</span>
		<div class="date-time">
			<span class="highlight">{{day}}</span>
			<span>{{month}}</span>
		</div>
		<div class="live" v-if="live">
			<span>live now!</span>
		</div>
		<div class="date-time" v-else>
			<span class="highlight">{{time}}</span>
			<span>{{timezone}}</span>
		</div>
	</div>
</template>

<script>
import MomentTZ from 'moment-timezone'
export default {
	name: 'date-time-slug',
	computed: {
		eventMoment: function () {
			return MomentTZ(this.event.start_dt)
		},
		weekDate: function () {
			return this.eventMoment.format('dddd').toUpperCase()
		},
		day: function () {
			return this.eventMoment.format('D')
		},
		month: function () {
			return this.eventMoment.format('MMM').toUpperCase()
		},
		time: function () {
			return this.eventMoment.format('LT')
		},
		timezone: function () {
			return MomentTZ.tz(MomentTZ.tz.guess()).format('z')
		}
	},
	props: ['event', 'live']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.slug{
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
	.highlight{
		color: $font-white;
	}
	.date-time{
		color: $font-gray;
		white-space: nowrap;
	}
	.live{
		padding: 5px;
    margin-top: 5px;
	}
}
</style>


