<template>
	<div class="router-page">
		<header-bar :showGold="false"></header-bar>
		<twitch-embed :target="streamer"></twitch-embed>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
import TwitchEmbed from './stream/TwitchEmbed'
import HeaderBar from './globals/Header'
import FooterBar from './globals/Footer'

export default {
	name: 'stream',
	components: {
		TwitchEmbed,
		HeaderBar,
		FooterBar
	},
	props: ['streamer']
}
</script>


