<template>
	<div id="nav-wrapper">
		<div id="gold-header" v-show="showGold">
			<div class="logo-wrap">
				<img class="gold-logo" src="../../assets/graphics/logo-small.png" alt="catalyst-logo-small"/>
				<span>TEAMCATALYST.TV</span>
			</div>
			<social-media-icons :iconColor="'#5a5c64'" :size="'20px'" class="social"></social-media-icons>
		</div>
		<nav id="nav-bar">
			<a class='logo' href="./#/" title="Return to home"><img src="../../assets/graphics/logo.png" alt="catalyst-logo"/></a>	
			<ul class="menu" role="menubar">
				<li class="nav-link" role="menuitem"><a href="./#/streams">STREAMS</a></li><li class="nav-link" role="menuitem"><a href="./#/events/">EVENTS</a></li><li class="nav-link" role="menuitem"><a href="https://www.twitch.tv/team/catalyst/">TWITCH</a></li><li class="nav-link" role="menuitem"><a href="./#/about" aria-label="About us">ABOUT US</a></li>
			</ul>
		</nav>
	</div>
</template>

<script>
import SocialMediaIcons from './SocialMediaIcons'

export default {
	name: 'header-bar',
	props: ['showGold'],
	components: {SocialMediaIcons}
}
window.onscroll = function () {
	var scrollTop = (window.pageYOffset !== undefined) ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop
	if (scrollTop < 400) {
		document.getElementById('gold-header').style['margin-bottom'] = '0'
		document.getElementById('nav-bar').style.position = 'static'
	} else if (scrollTop >= 400 && scrollTop < 600) {
		document.getElementById('nav-bar').style.top = -100
	} else {
		document.getElementById('nav-bar').style.position = 'fixed'
		document.getElementById('gold-header').style['margin-bottom'] = '70px'
		document.getElementById('nav-bar').style.top = 0
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../../variables.scss';
#gold-header{
	background-color: $yellow-dark;
	background-image: url('../../assets/graphics/gold-background.png');
	position:relative;
	height: 100px;
	.logo-wrap{
		color: $gray-light;
		position: absolute;
		white-space: nowrap;
		right: 95%;
		left: 5%;
		top: 12%;
		font-weight: bolder;
		font-size: 18px;
		transition: $standard-transition;
	}
	.gold-logo{
		width:75px;
		vertical-align: middle;
	}
}
#nav-bar{
	transition: all .5s ease-in-out;
	background-color: $gray-dark;
	white-space: nowrap;
	overflow: hidden;
	min-height: 70px;
	min-width: 500px;
	width: 100%;
	top: -100;
	z-index: 1;
	box-shadow: $bs;
}
.nav-link{
	display: inline-block;
	line-height: 52px;
	transition: $standard-transition;
	a{
		transition: $standard-transition;
		color: $yellow-dark;
		display: block;
		padding: 10px;
		width: 85px;
		height: 52px;
		&:hover, &:focus{
			background-color: $yellow-dark;
			color: $gray-dark;	
		}
		&:active{
			background-color: $yellow-light;
			color: $gray-dark;
		}
	}
}
.social{
	position: absolute;
	right: 5%;
	top: 26%;
}
.menu{
	display: inline-block;
	position: absolute;
	right: 35px;
	font-size: 14px;
	white-space: nowrap;
	margin: 0;
	padding: 0;
}
.logo{
	display: inline-block;
	vertical-align: middle;
	border-radius: 100px;
	margin: 8px 10px;
	width: 275px;
	overflow: hidden;
	transition: all .3s linear;
	img{
		width:275px;
	}
	&:focus{
		outline: auto;
	}
}
@media screen and (max-width: 77.000em){
	.menu{
		position: static;
	}
}
@media screen and (max-width: 55.000em){
	.nav-link a{
		width: 72px;
	}
}
@media screen and (max-width: 45.000em){
	.logo{
		width: 70px;
	}
	.social{
		display: none;
	}
	#gold-header .logo-wrap{
		right: 0;
		left: 0;
	}
}
</style>


