<template>
	<div class="member-tile" v-bind:id="member.display_name+'-tile'">
		<img :src="logo" :alt="member.display_name + '-logo'">
		<h2>{{member.display_name}}</h2>
		<div class="live" v-if="live">live! {{viewers}}
			<svg height="16px" version="1.1" viewBox="0 0 16 16" width="16px" x="0px" y="-10px" class="svg-glyph_live">
				<path clip-rule="evenodd" d="M11,14H5H2v-1l3-3h2L5,8V2h6v6l-2,2h2l3,3v1H11z" fill-rule="evenodd"></path>
			</svg>
		</div>
		<template v-if="member.role==='streamer'">
			<a class=" yellow twitch-url" :href="url">{{url.slice(12)}}</a>
			<div class="followers" :class="{invisible: followers === 'hidden'}">{{followers}}</div>
			<div class="yellow">{{schedule}}</div>
			<div class="yellow" :class="{invisible: schedule2 === 'none'}">{{schedule2}}</div>
		</template>
		<template v-else>
			<div class="role">{{member.role}}</div>
			<a :href="url" class="twitch-url"> {{url.slice(12)}}</a>
			<a :href="member.social_media.email" class="manager-email yellow">{{member.email}}</a>
		</template>
		<p class="bio">{{member.bio}}</p>
		<a class="icon" :href="value" :title="member.display_name + ' ' + key" :class="key" v-for="(value, key) in member.social_media"></a>
	</div>
</template>

<script>
export default {
	name: 'member-tile',
	computed: {
		twitchData: function () {
			return this.$store.state.TwitchInfo[this.member.id]
		},
		schedule: function () {
			return this.member.schedule
		},
		schedule2: function () {
			return this.member.schedule2 ? this.member.schedule2 : 'none'
		},
		logo: function () {
			return this.twitchData ? this.twitchData.logo : '../../../static/profiles/' + this.member.display_name + '.png'
		},
		url: function () {
			return this.twitchData ? this.twitchData.url : 'https://www.twitch.tv/' + this.member.display_name
		},
		live: function () {
			return this.twitchData ? this.twitchData.live : null
		},
		viewers: function () {
			return this.live ? this.live.viewers.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') : 0
		},
		followers: function () {
			return this.twitchData ? this.twitchData.followers.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') + ' Followers' : 'hidden'
		}
	},
	props: ['member']
}
</script>

<style scoped lang="scss">
@import "../../variables.scss";
h2{
	display: inline-block;
	text-transform: capitalize;
	color: $font-gray;
}
.member-tile {
	width: 325px;
	display: inline-block;
	margin: 15px;
	vertical-align: top;
	img {
		display: block;
		width: 125px;
		margin: 7px auto;
		border-radius: 125px;
		padding: 4px;
	}
	.twitch-url{
		display:block;
		margin: 5px 0;
	}
	.invisible{
		visibility: hidden;
	}
	.followers{
		margin: 5px 0;
	}
	.live{
		vertical-align: top;
	}
	.bio{
		margin: 5px;
	}
	.role{
		color: $yellow-dark;
		font-size: 16px;
		margin: 5px 0;
	}
	.manager-email{
		display: block;
		margin-bottom: 24px;
	}
}
#lowco2525-tile img{
	border: 2px solid $lowco-purple;
}
#smashley-tile img{
	border: 2px solid $smashley-blue;
}
#djtechlive-tile img{
	border: 2px solid $dj-blue;
}
#bloodyfaster-tile img{
	border: 2px solid $bloody-red;
}
#odysseygrrrl-tile img{
	border: 2px solid $yellow-dark;
}
.icon {
	display: inline-block;
	transition: all .2s ease-in-out;
	width: 25px;
	height: 25px;
	margin: 5px;
	-webkit-filter: grayscale(100%) brightness(75%);
	-moz-filter: grayscale(100%) brightness(75%);
	-ms-filter: grayscale(100%) brightness(75%);
	-o-filter: grayscale(100%) brightness(75%);
		filter: grayscale(100%) brightness(75%);
	&:hover{
		-webkit-filter: grayscale(0%) brightness(100%);
		-moz-filter: grayscale(0%) brightness(100%);
		-ms-filter: grayscale(0%) brightness(100%);
		-o-filter: grayscale(0%) brightness(100%);
			filter: grayscale(0%) brightness(100%);
	}
	&.twitter {
		background: url("../../assets/svg/twitter.svg") no-repeat 50% 50%;
	}
	&.discord {
		background: url("../../assets/svg/discord.svg") no-repeat 50% 50%;
	}
	&.email {
		background: url("../../assets/svg/email.svg") no-repeat 50% 50%;
		background-size: cover;
	}
	&.instagram {
		background: url("../../assets/svg/instagram.svg") no-repeat 50% 50%;
	}
	&.youtube {
		background: url("../../assets/svg/youtube.svg") no-repeat 50% 50%;
	}
}
</style>


