<template>
	<div class="about router-page">
		<header-bar :showGold="false"></header-bar>
		<div class="tile-wrapper">
			<about-tile v-for="member in members" :member="member"></about-tile>
		</div>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
import Members from '../../static/memberInfo.json'
import HeaderBar from './globals/Header'
import AboutTile from './about/AboutTile'
import FooterBar from './globals/Footer'
import { mapActions } from 'vuex'

export default {
	name: 'about',
	components: {
		HeaderBar,
		AboutTile,
		FooterBar
	},
	data () {
		return {
			members: Members
		}
	},
	methods: {
		...mapActions([
			'updateChannelInfoList',
			'getChannelInfo'
		])
	},
	mounted () {
		this.updateChannelInfoList()
	}
}
</script>

<style scoped lang="scss">
@import "../variables.scss";
.tile-wrapper{
	margin:25px 0;
}
</style>



