<template>
	<a :href="url" target="_blank" v-if="role" class="member-tile" :id="streamer+'-tile'" :aria-label="'team member ' + streamer">
		<img :src="logo" :alt="streamer + '-logo'">
		<div class="info-section">
			<h2>{{streamer}}</h2>
			<p>{{description}}</p>
			<h3>{{url.slice(12)}}</h3>
			<div class="schedule" v-if="!live">{{schedule}}</div>
			<div class="schedule" v-if="!live && schedule2 != null">{{schedule2}}</div>
			<div class="live" v-if="live">live! {{viewers}}
        <svg height="16px" version="1.1" viewBox="0 0 16 16" width="16px" x="0px" y="-10px" class="svg-glyph_live">
					<path clip-rule="evenodd" d="M11,14H5H2v-1l3-3h2L5,8V2h6v6l-2,2h2l3,3v1H11z" fill-rule="evenodd"></path>
				</svg>
      </div>
		</div>
	</a>
</template>

<script>
import Members from '../../../static/memberInfo.json'
export default {
	name: 'member-tile',
	computed: {
		twitchData () {
			return this.$store.state.TwitchInfo[Members[this.streamer].id]
		},
		description () {
			return Members[this.streamer].description
		},
		schedule () {
			return Members[this.streamer].schedule
		},
		schedule2 () {
			return Members[this.streamer].schedule2
		},
		role () {
			return Members[this.streamer].role === 'streamer'
		},
		url () {
			return this.twitchData ? this.twitchData.url : 'https://www.twitch.tv/' + this.streamer
		},
		logo () {
			return this.twitchData ? this.twitchData.logo : '../../../static/profiles/' + this.streamer + '.png'
		},
		live () {
			return this.twitchData ? this.twitchData.live : null
		},
		viewers () {
			return this.live ? this.twitchData.live.viewers : 0
		}
	},
	props: ['streamer']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
img{
	border-radius: 100px;
	width: 135px;
	border: 2px solid $gray-light;
	padding: 4px;
	margin-right: 15px;
	transition: $standard-transition;
}
p{
	margin: 5px 0;
}
.svg-glyph_live {
	vertical-align: middle;
}
.live{
	margin-top: 2px;
}
.member-tile{
	display: inline-block;
	margin: 25px 2%;
	padding: 10px;
	border-radius: 10px;
	transition: $standard-transition;
}
.member-tile:hover, .member-tile:focus {
	background-color: $gray-dark;
	box-shadow: 0 0 2px $gray-light;
}
.info-section{
	display: inline-block;
	vertical-align: top;
	text-align: left;
	max-width: 325px;
}
#lowco2525-tile:hover img, #lowco2525-tile:focus img{
	border-color: $lowco-purple;
}
#smashley-tile:hover img, #smashley-tile:focus img{
	border-color: $smashley-blue;
}
#djtechlive-tile:hover img, #djtechlive-tile:focus img{
	border-color: $dj-blue;
}
#bloodyfaster-tile:hover img, #bloodyfaster-tile:focus img{
	border-color: $bloody-red;
}
@media screen and (max-width: 80.000em){ /*960px*/
	.info-section{
		max-width: 275px;
	}
}
</style>


