<template>
	<div class="calendar-tile">
		<div class="first-day" :class="{gray: eventOver}" v-if="firstEvent">
			<div class="details-wrap">
				<img :src="logo" :class="displayName.toLowerCase()" :alt="displayName + '-logo'">
				<div class="title-wrap">
					<div class="streamer" :class="displayName.toLowerCase()">{{displayName}}</div>
					<div class="game-title">{{firstEvent.title}}{{game}}</div>
				</div>
			</div>
			<date-time-slug :event="firstEvent" :live="showLive"></date-time-slug>
		</div>
		<div class="second-day" :class="{'visibile': !secondEvent}">
			<div class="title-wrap" v-if="secondEvent">
				<div class="streamer" :class="displayName.toLowerCase()">{{displayName}}</div>
				<div class="game-title" >{{secondEvent.title}}</div>
			</div>
			<date-time-slug :event="secondEvent" v-if="secondEvent"></date-time-slug>
		</div>
	</div>
</template>

<script>
import Members from '../../../static/memberInfo.json'
import DateTimeSlug from './DateTimeSlug'
import MomentTZ from 'moment-timezone'
export default {
	name: 'calendar-tile',
	computed: {
		displayName () {
			for (var member in Members) {
				if (Members[member].calendar_id.toString() === this.calendarId) {
					return member
				}
			}
			return 'non-member'
		},
		firstEvent () {
			return this.calendar[0]
		},
		secondEvent () {
			return this.calendar[1]
		},
		logo () {
			var twitchId = Members[this.displayName].id
			return this.$store.state.TwitchInfo[twitchId] ? this.$store.state.TwitchInfo[twitchId].logo : '../../../static/profiles/' + this.displayName + '.png'
		},
		game () {
			// check to see if the event has started
			var eventStart = MomentTZ(this.firstEvent.start_dt)
			if ((eventStart.isBefore(MomentTZ()) && this.live) || this.eventOver) {
				if (this.$store.state.TwitchInfo[Members[this.displayName].id]) {
					return ' - ' + this.$store.state.TwitchInfo[Members[this.displayName].id].game
				}
			}
		},
		live () {
			return this.$store.state.TwitchInfo[Members[this.displayName].id] ? this.$store.state.TwitchInfo[Members[this.displayName].id].live : null
		},
		showLive () {
			var eventStart = MomentTZ(this.firstEvent.start_dt)
			return this.live && eventStart.isBefore(MomentTZ())
		},
		eventOver () {
			var eventEnd = MomentTZ(this.firstEvent.end_dt)
			var isBefore = MomentTZ(eventEnd).isBefore(MomentTZ())
			return isBefore && !this.live
		}
	},
	components: {
		DateTimeSlug
	},
	props: ['calendar', 'calendarId']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.visibile{visibility: hide;}
.gray{
	filter: grayscale(1);
	.streamer{
		color: $font-white;
	}
}
.calendar-tile{
	display: flex;
	flex-wrap: wrap;
	&:not(:first-child){
		margin: 10px 0;
	}
	.first-day, .second-day {
		display: flex;
		justify-content: space-between;
		box-shadow: $bs;
		padding: 15px;
		margin-right: 10px;
	}
	.first-day{
		flex: 2 1 330px;
		background-color: $gray-dark;
		border-left: 2px solid $yellow-dark;
	}
	.second-day{
		flex: 1 1 230px;
		background-color: $calendar-dark;
	}
	.title-wrap{
		white-space: normal;
		padding-right: 10px;
		align-self: center;
	}
	.details-wrap{
		white-space: nowrap;
		display: flex;
		align-items: center;
	}
}
img{
	border-radius: 100px;
	width: 65px;
	height: 65px;
	border: 2px solid $gray-light;
	padding: 3px;
	margin-right: 10px;
	vertical-align: middle; 
}
.game-title{
	margin: 5px 0;
}
.streamer{
	text-transform: capitalize;
}
.lowco2525{
	color: $lowco-purple;
	border-color: $lowco-purple;
}
.smashley{
	color: $smashley-blue;
	border-color: $smashley-blue;
}
.djtechlive{
	color: $dj-blue;
	border-color: $dj-blue;
}
.bloodyfaster{
	color: $bloody-red;
	border-color: $bloody-red;
}
.team-event{
	color: $yellow-dark;
	border-color: $yellow-dark;
}
</style>


