<template>
	<div class="events">
		<section-header :sectionName="sectionName" class="custom-header"></section-header>
		<div class="section-wrapper">
			<div class="calendar-wrapper">
				<calendar-row :calendar="value" :calendarId="key" v-for="(value, key) in calendarGroupedByCalendar.personnelCalendars" v-show="value.length > 0"></calendar-row>
			</div>
			<div class="team-event-wrap" v-show="teamCalendarLength > 0">
				<team-event :event="selectedTeamEvent"></team-event>
				<div class="arrow-wrap">
					<button class="arrow left" :class="{disabled: selectedTeamEventIndex === 0}" aria-label="Previous event" v-on:click="changeTeamEvent(-1)">
						<svg viewBox="0 0 50 80" xml:space="preserve">
							<polyline points=" 45.63,75.8 0.375,38.087 45.63,0.375 "/>
						</svg>  
					</button>
					<button class="arrow right" :class="{disabled: selectedTeamEventIndex >= teamCalendarLength - 1}" aria-label="Next event" v-on:click="changeTeamEvent(1)">
						<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 50 80" xml:space="preserve">
							<polyline points=" 0.375,0.375 45.63,38.087 0.375,75.8 "/>
						</svg>
					</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapActions } from 'vuex'
import SectionHeader from './SectionHeader'
import CalendarRow from '../calendar/DateTile'
import TeamEvent from '../calendar/TeamEvent'
import MomentTZ from 'moment-timezone'
import Members from '../../../static/memberInfo.json'

const TEAM_EVENTS_CALENDAR_ID = 2718538

export default {
	name: 'events',
	data () {
		return {
			calendarInfo: {},
			calendarGroupedByCalendar: {},
			selectedTeamEventIndex: 0,
			teamCalendarLength: 0,
			sectionName: 'CALENDAR'
		}
	},
	components: {
		SectionHeader,
		CalendarRow,
		TeamEvent
	},
	methods: {
		changeTeamEvent: function (direction) {
			if ((this.selectedTeamEventIndex + direction < this.teamCalendarLength) && (this.selectedTeamEventIndex + direction >= 0)) {
				this.selectedTeamEventIndex = this.selectedTeamEventIndex + direction
			}
		},
		sortCalendarDataByCalendar: function (calendarData, targetSet) {
			var _personnelCalendars = {}
			var _teamCalendar = {}

			// preload calendarLists
			for (var member in Members) {
				if (Members[member].calendar_id !== -1) {
					_personnelCalendars[Members[member].calendar_id] = []
				}
			}
			_teamCalendar[TEAM_EVENTS_CALENDAR_ID] = []

			// iterate through the events
			for (var event in calendarData[targetSet].events) {
				var thisEvent = calendarData[targetSet].events[event]
				// iterate through each calendar in that sub calendar
				for (var subCal in thisEvent.subcalendar_ids) {
					var thisCalendar = thisEvent.subcalendar_ids[subCal]
					// divide the calendars between group calendars and streamer calendars
					if (_personnelCalendars[thisCalendar]) {
						_personnelCalendars[thisCalendar].push(thisEvent)
					} else if (_teamCalendar[thisCalendar]) {
						_teamCalendar[thisCalendar].push(thisEvent)
					}
				}
			}
			this.teamCalendarLength = _teamCalendar[TEAM_EVENTS_CALENDAR_ID].length
			return {personnelCalendars: _personnelCalendars, teamCalendar: _teamCalendar[TEAM_EVENTS_CALENDAR_ID]}
		},
		isPersonalCal: function (calendarId) {
			for (var member in Members) {
				if (Members[member].calendar_id === calendarId) {
					return true
				}
			}
			return false
		},
		parseCalendars: function (calendarData, targetWeek) {
			this.calendarGroupedByCalendar = this.sortCalendarDataByCalendar(calendarData, targetWeek)
			console.log(this.calendarGroupedByCalendar)
		},
		filterList: function (target) {
			for (var date in this.calendarGroupedByStreamer) {
				var day = this.calendarGroupedByStreamer[date]
				for (var event in day) {
					if (day[event].subcalendar_ids[0] === target) {
						day[event].show = !day[event].show
					}
				}
			}
		},
		...mapActions([
			'updateGroupCalendar'
		])
	},
	computed: {
		selectedTeamEvent: function () {
			return this.calendarGroupedByCalendar.teamCalendar ? this.calendarGroupedByCalendar.teamCalendar[this.selectedTeamEventIndex] : {}
		}
	},
	mounted () {
		var startDate = MomentTZ().startOf('day').format('YYYY-MM-DD')
		if (this.$store.state.CalendarInfo[startDate]) {
			this.calendarInfo = this.$store.state.CalendarInfo
			this.parseCalendars(this.calendarInfo, startDate)
		} else {
			var endDate = MomentTZ().add(1, 'month').format('YYYY-MM-DD')
			this.updateGroupCalendar({'startDate': startDate, 'endDate': endDate}).then(function () {
				this.calendarInfo = this.$store.state.CalendarInfo
				this.parseCalendars(this.calendarInfo, startDate)
			}.bind(this))
		}
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.section-wrapper{
	display: flex;
	flex-wrap: wrap-reverse;
	width: 90%;
	max-width: 1250px;
	margin: 25px auto;
	padding-left: 10px;
}
.calendar-wrapper{
	flex: 20 1 370px;
}
.team-event-wrap{
	position: relative;
	display: flex;
	flex: 1 0.25 280px;
	margin: 0 10px 10px 0;
}
.arrow-wrap {
	position: absolute;
	right: 15px;
	top: 15px;
}
.arrow{
	cursor: pointer;
	stroke: $yellow-dark;
	svg{
		height: 20px;
		width: 20px;
		polyline{
			fill: none;
			stroke-width: 5;
		}
	}
	&:hover polyline,
	&:focus polyline {
		stroke-width: 10;
	}
	&:active polyline {
		stroke-width: 12;
		transition: all 100ms ease-in-out;
	}
	polyline {
		transition: all 250ms ease-in-out;
	}
	&.disabled{
		cursor: context-menu;
		stroke: $gray-light;
		&:hover polyline,
		&:focus polyline {
			stroke-width: 5;
		}
		&:active polyline {
			stroke-width: 5;
			transition: all 100ms ease-in-out;
		}
	}
}
</style>


